export default function DashboardPage() {
  const [items, setItems] = useState<ComparisonItem[]>([]);
  const [loading, setLoading] = useState(true);
  const [refreshing, setRefreshing] = useState(false);
  const [pinging, setPinging] = useState(false);
  const [apiOk, setApiOk] = useState<boolean | null>(null);
  const [error, setError] = useState<string | null>(null);
  const [role, setRole] = useState<Role>("MANAGER");
  const [activeTab, setActiveTab] = useState<"overview" | "documents" | "rag">("overview");

  useEffect(() => {
    setRole(getStoredRole());
    fetchData();
  }, []);

  useEffect(() => {
    setStoredRole(role);
  }, [role]);

  const fetchData = async (isRefresh = false) => {
    if (isRefresh) {
      setRefreshing(true);
    } else {
      setLoading(true);
    }
    setError(null);
    
    try {
      const res = await fetch(`${API_BASE}/comparisons?limit=100`, {
        cache: "no-store",
      });
      if (!res.ok) throw new Error(`Failed to load data (${res.status})`);
      const data: ComparisonItem[] = await res.json();
      setItems(Array.isArray(data) ? data : []);
      setApiOk(true);
    } catch (e: any) {
      setError(e?.message || "Failed to load data");
      setApiOk(false);
      setItems([]);
    } finally {
      setLoading(false);
      setRefreshing(false);
    }
  };

  const pingApi = async () => {
    setPinging(true);
    try {
      const res = await fetch(`${API_BASE}/health`, { cache: "no-store" });
      setApiOk(res.ok);
    } catch {
      setApiOk(false);
    } finally {
      setPinging(false);
    }
  };

  const computed = useMemo(() => {
    const list = [...items].sort(
      (a, b) =>
        new Date(b.created_at).getTime() - new Date(a.created_at).getTime()
    );

    const comparisons = list.length;
    const docsSet = new Set(list.map((x) => x.document_name).filter(Boolean));
    const documents = docsSet.size;

    let high = 0, med = 0, low = 0;
    list.forEach((x) => {
      const r = normalizeRisk(x.overall_risk_level);
      if (r === "HIGH") high++;
      else if (r === "MEDIUM") med++;
      else low++;
    });

    const latest = list.slice(0, 6);
    const totalChanges = list.reduce((sum, item) => sum + (item.changes_count || 0), 0);
    const avgChanges = comparisons > 0 ? Math.round(totalChanges / comparisons) : 0;

    // Recent activity (last 7 days)
    const now = new Date();
    const recentActivity = list.filter((x) => {
      const d = new Date(x.created_at);
      const diff = Math.floor((now.getTime() - d.getTime()) / (1000 * 60 * 60 * 24));
      return diff < 7;
    }).length;

    return {
      list,
      comparisons,
      documents,
      high,
      med,
      low,
      latest,
      totalChanges,
      avgChanges,
      recentActivity,
      riskPercentage: comparisons > 0 ? ((high + med) / comparisons * 100).toFixed(1) : "0",
    };
  }, [items]);

  const canSeeRisk = role !== "VIEWER";
  const canDownload = role === "ADMIN" || role === "MANAGER";

  const sidebarItems = [
    { id: "dashboard", icon: <HomeIcon className="h-5 w-5" />, label: "Dashboard", active: true, href: "/dashboard" },
    { id: "compare", icon: <DocumentMagnifyingGlassIcon className="h-5 w-5" />, label: "Compare", badge: computed.comparisons, href: "/" },
    { id: "history", icon: <ClockIcon className="h-5 w-5" />, label: "History", href: "/history" },
    { id: "reports", icon: <DocumentChartBarIcon className="h-5 w-5" />, label: "Reports", href: "/reports/generate" },
    { id: "rag", icon: <ArchiveBoxIcon className="h-5 w-5" />, label: "RAG Storage", href: "#" },
  ];

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-50/30">
      {/* Header */}
      <header className="sticky top-0 z-50 bg-white/80 backdrop-blur-xl border-b border-slate-200">
        <div className="max-w-7xl mx-auto px-4">
          <div className="flex items-center justify-between h-16">
            {/* Logo */}
            <div className="flex items-center gap-3">
              <Link href="/" className="flex items-center gap-2.5 group">
                <div className="w-9 h-9 rounded-xl bg-gradient-to-br from-blue-600 to-blue-700 flex items-center justify-center shadow-sm">
                  <DocumentMagnifyingGlassIcon className="h-5 w-5 text-white" />
                </div>
                <div>
                  <div className="text-lg font-bold text-slate-900">DocAnalyzer</div>
                  <div className="text-xs text-slate-500">AI Document Analysis</div>
                </div>
              </Link>
              
              <div className="hidden md:flex items-center ml-6">
                <div className="relative">
                  <MagnifyingGlassIcon className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-slate-400" />
                  <input
                    type="search"
                    placeholder="Search documents..."
                    className="pl-10 pr-4 py-2 bg-slate-100 rounded-lg text-sm w-64 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:bg-white transition-all duration-200"
                  />
                </div>
              </div>
            </div>

            {/* Right Actions */}
            <div className="flex items-center gap-3">
              {/* Status */}
              <div className="hidden sm:flex items-center gap-2 px-3 py-2 bg-slate-100 rounded-lg">
                <div className={`h-2 w-2 rounded-full ${apiOk ? 'bg-emerald-500' : apiOk === false ? 'bg-red-500' : 'bg-slate-400'}`} />
                <span className="text-sm text-slate-600">
                  {pinging ? 'Checking...' : apiOk ? 'API Online' : apiOk === false ? 'API Offline' : 'Unknown'}
                </span>
              </div>

              {/* Role Selector */}
              <div className="relative group">
                <button className="flex items-center gap-2 px-3 py-2 bg-blue-50 text-blue-700 rounded-lg hover:bg-blue-100 transition-colors text-sm font-semibold">
                  <UserCircleIcon className="h-4 w-4" />
                  {role}
                  <ChevronRightIcon className="h-3 w-3 transform group-hover:rotate-90 transition-transform" />
                </button>
                <div className="absolute right-0 top-full mt-1 w-32 bg-white rounded-lg shadow-lg border border-slate-200 opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-all duration-200 z-50">
                  {["ADMIN", "MANAGER", "VIEWER"].map((r) => (
                    <button
                      key={r}
                      onClick={() => setRole(r as Role)}
                      className={`w-full text-left px-4 py-2 text-sm hover:bg-slate-50 transition-colors first:rounded-t-lg last:rounded-b-lg ${
                        role === r ? 'text-blue-600 font-semibold' : 'text-slate-700'
                      }`}
                    >
                      {r}
                    </button>
                  ))}
                </div>
              </div>

              {/* Action Buttons */}
              <button
                onClick={() => fetchData(true)}
                disabled={refreshing}
                className="p-2 text-slate-600 hover:text-slate-900 hover:bg-slate-100 rounded-lg transition-colors disabled:opacity-50"
                title="Refresh"
              >
                <ArrowPathIcon className={`h-5 w-5 ${refreshing ? 'animate-spin' : ''}`} />
              </button>

              <button className="p-2 relative">
                <BellIcon className="h-5 w-5 text-slate-600 hover:text-slate-900 transition-colors" />
                {computed.high > 0 && (
                  <span className="absolute -top-1 -right-1 h-4 w-4 bg-red-500 text-white text-xs rounded-full flex items-center justify-center font-bold">
                    {Math.min(computed.high, 9)}
                  </span>
                )}
              </button>

              <button className="p-2">
                <Cog6ToothIcon className="h-5 w-5 text-slate-600 hover:text-slate-900 transition-colors" />
              </button>
            </div>
          </div>
        </div>
      </header>

      <div className="max-w-7xl mx-auto px-4 py-6">
        <div className="flex flex-col lg:flex-row gap-6">
          {/* Sidebar */}
          <aside className="lg:w-64 flex-shrink-0">
            <ModernCard hover={false} className="sticky top-24">
              <div className="p-4">
                {/* User Profile */}
                <div className="mb-6 p-3 bg-gradient-to-r from-blue-50 to-blue-100/50 rounded-lg">
                  <div className="flex items-center gap-3">
                    <div className="w-10 h-10 rounded-lg bg-gradient-to-br from-blue-600 to-blue-700 flex items-center justify-center text-white font-semibold">
                      {role.charAt(0)}
                    </div>
                    <div>
                      <div className="font-semibold text-slate-900">{role} User</div>
                      <div className="text-xs text-slate-500">Active now</div>
                    </div>
                  </div>
                </div>

                {/* Navigation */}
                <div className="space-y-1">
                  {sidebarItems.map((item) => (
                    <button
                      key={item.id}
                      onClick={() => {
                        if (item.id === "rag") {
                          setActiveTab("rag");
                        } else if (item.href !== "#") {
                          window.location.href = item.href;
                        }
                      }}
                      className={`
                        w-full text-left flex items-center justify-between gap-3 px-3 py-2.5 rounded-lg
                        transition-all duration-200 group
                        ${activeTab === item.id || item.active 
                          ? 'bg-blue-50 text-blue-700 font-semibold' 
                          : 'text-slate-700 hover:bg-slate-50 hover:text-slate-900'
                        }
                      `}
                    >
                      <div className="flex items-center gap-3">
                        <div className={activeTab === item.id || item.active ? 'text-blue-600' : 'text-slate-500 group-hover:text-slate-700'}>
                          {item.icon}
                        </div>
                        <span className="text-sm">{item.label}</span>
                      </div>
                      {item.badge && (
                        <span className="bg-blue-100 text-blue-700 text-xs font-semibold px-2 py-0.5 rounded-full">
                          {item.badge}
                        </span>
                      )}
                    </button>
                  ))}
                </div>

                {/* Quick Actions */}
                <div className="mt-8">
                  <div className="text-xs font-semibold text-slate-500 uppercase tracking-wider mb-3 px-3">
                    Quick Actions
                  </div>
                  <div className="space-y-2">
                    <Link
                      href="/"
                      className="block w-full bg-gradient-to-r from-blue-600 to-blue-700 text-white hover:from-blue-700 hover:to-blue-800 active:scale-[0.98] shadow-sm hover:shadow rounded-lg font-semibold transition-all duration-200 flex items-center justify-center gap-2 px-4 py-3 text-sm"
                    >
                      <DocumentMagnifyingGlassIcon className="h-4 w-4" />
                      New Comparison
                    </Link>
                    
                    <Link
                      href="/rag/upload"
                      className="block w-full bg-gradient-to-r from-emerald-600 to-emerald-700 text-white hover:from-emerald-700 hover:to-emerald-800 active:scale-[0.98] shadow-sm hover:shadow rounded-lg font-semibold transition-all duration-200 flex items-center justify-center gap-2 px-4 py-3 text-sm"
                    >
                      <ArchiveBoxIcon className="h-4 w-4" />
                      Upload to RAG
                    </Link>
                  </div>
                </div>
              </div>
            </ModernCard>
          </aside>

          {/* Main Content */}
          <main className="flex-1 min-w-0">
            {/* Tab Navigation */}
            <div className="mb-6">
              <ModernCard>
                <div className="p-4">
                  <div className="flex items-center space-x-2">
                    <button
                      onClick={() => setActiveTab("overview")}
                      className={`px-4 py-2 rounded-lg text-sm font-semibold transition-all duration-200 ${
                        activeTab === "overview"
                          ? 'bg-gradient-to-r from-blue-600 to-blue-700 text-white shadow-sm'
                          : 'text-slate-600 hover:text-slate-900 hover:bg-slate-100'
                      }`}
                    >
                      Overview
                    </button>
                    <button
                      onClick={() => setActiveTab("documents")}
                      className={`px-4 py-2 rounded-lg text-sm font-semibold transition-all duration-200 ${
                        activeTab === "documents"
                          ? 'bg-gradient-to-r from-blue-600 to-blue-700 text-white shadow-sm'
                          : 'text-slate-600 hover:text-slate-900 hover:bg-slate-100'
                      }`}
                    >
                      Documents ({computed.documents})
                    </button>
                    <button
                      onClick={() => setActiveTab("rag")}
                      className={`px-4 py-2 rounded-lg text-sm font-semibold transition-all duration-200 ${
                        activeTab === "rag"
                          ? 'bg-gradient-to-r from-blue-600 to-blue-700 text-white shadow-sm'
                          : 'text-slate-600 hover:text-slate-900 hover:bg-slate-100'
                      }`}
                    >
                      RAG Storage
                    </button>
                  </div>
                </div>
              </ModernCard>
            </div>

            {/* Overview Tab */}
            {activeTab === "overview" && (
              <>
                {/* Welcome Section */}
                <div className="mb-6">
                  <ModernCard className="bg-gradient-to-r from-blue-600 to-blue-700">
                    <div className="p-6">
                      <div className="flex flex-col md:flex-row md:items-center justify-between gap-4">
                        <div>
                          <h1 className="text-2xl font-bold text-white mb-2">Welcome back! üëã</h1>
                          <p className="text-blue-100">
                            You have {computed.recentActivity} new comparisons this week
                          </p>
                        </div>
                        <div className="flex flex-wrap gap-2">
                          <Link
                            href="/"
                            className="bg-gradient-to-r from-emerald-600 to-emerald-700 text-white hover:from-emerald-700 hover:to-emerald-800 active:scale-[0.98] shadow-sm hover:shadow rounded-lg font-semibold transition-all duration-200 flex items-center justify-center gap-2 px-4 py-3 text-sm"
                          >
                            <DocumentMagnifyingGlassIcon className="h-4 w-4" />
                            Start New Analysis
                          </Link>
                          
                          <button 
                            onClick={() => setActiveTab("rag")}
                            className="bg-transparent text-white border border-white/30 hover:bg-white/10 rounded-lg font-semibold transition-all duration-200 flex items-center justify-center gap-2 px-4 py-3 text-sm"
                          >
                            <ArchiveBoxIcon className="h-4 w-4" />
                            Go to RAG
                          </button>
                        </div>
                      </div>
                      <div className="mt-6 grid grid-cols-2 md:grid-cols-4 gap-4">
                        <div className="bg-white/10 rounded-lg p-3 backdrop-blur-sm">
                          <div className="text-blue-100 text-sm font-medium">Active</div>
                          <div className="text-white text-xl font-bold">{computed.documents}</div>
                        </div>
                        <div className="bg-white/10 rounded-lg p-3 backdrop-blur-sm">
                          <div className="text-blue-100 text-sm font-medium">Total</div>
                          <div className="text-white text-xl font-bold">{computed.comparisons}</div>
                        </div>
                        <div className="bg-white/10 rounded-lg p-3 backdrop-blur-sm">
                          <div className="text-blue-100 text-sm font-medium">Changes</div>
                          <div className="text-white text-xl font-bold">{computed.totalChanges}</div>
                        </div>
                        <div className="bg-white/10 rounded-lg p-3 backdrop-blur-sm">
                          <div className="text-blue-100 text-sm font-medium">Avg/File</div>
                          <div className="text-white text-xl font-bold">{computed.avgChanges}</div>
                        </div>
                      </div>
                    </div>
                  </ModernCard>
                </div>

                {/* Error Alert */}
                {error && (
                  <div className="mb-6">
                    <ModernCard className="border-red-200 bg-red-50">
                      <div className="p-4">
                        <div className="flex items-start gap-3">
                          <ExclamationCircleIcon className="h-5 w-5 text-red-500 flex-shrink-0 mt-0.5" />
                          <div className="flex-1">
                            <div className="font-semibold text-red-800">Failed to load data</div>
                            <div className="text-sm text-red-600 mt-1">{error}</div>
                          </div>
                          <button
                            onClick={() => fetchData(true)}
                            className="text-sm font-medium text-red-600 hover:text-red-800"
                          >
                            Retry
                          </button>
                        </div>
                      </div>
                    </ModernCard>
                  </div>
                )}

                {/* Stats Grid */}
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 mb-6">
                  <StatCard
                    title="Active Documents"
                    value={computed.documents}
                    icon={<DocumentTextIcon className="h-6 w-6" />}
                    trend="+12.5%"
                    subtitle="Currently monitored"
                    loading={loading}
                  />
                  <StatCard
                    title="Total Comparisons"
                    value={computed.comparisons}
                    icon={<ChartBarIcon className="h-6 w-6" />}
                    trend="+8.2%"
                    subtitle="All time"
                    loading={loading}
                  />
                  <StatCard
                    title="High Risk Items"
                    value={computed.high}
                    icon={<ExclamationTriangleIcon className="h-6 w-6" />}
                    trend="-3.1%"
                    subtitle="Requires attention"
                    loading={loading}
                  />
                  <StatCard
                    title="Avg Processing Time"
                    value="<2s"
                    icon={<ClockIcon className="h-6 w-6" />}
                    trend="+15%"
                    subtitle="System performance"
                    loading={loading}
                  />
                </div>

                {/* Two Column Layout */}
                <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                  {/* Recent Activity */}
                  <ModernCard>
                    <div className="p-5">
                      <div className="flex items-center justify-between mb-4">
                        <div>
                          <h2 className="text-lg font-semibold text-slate-900">Recent Activity</h2>
                          <p className="text-sm text-slate-500 mt-1">Latest document comparisons</p>
                        </div>
                        <Link 
                          href="/history" 
                          className="text-sm font-medium text-blue-600 hover:text-blue-700"
                        >
                          View All
                        </Link>
                      </div>
                      
                      <div className="space-y-1">
                        {loading ? (
                          <div className="py-8 text-center">
                            <ArrowPathIcon className="h-8 w-8 animate-spin mx-auto text-slate-400" />
                            <p className="mt-3 text-sm text-slate-500">Loading activities...</p>
                          </div>
                        ) : computed.latest.length === 0 ? (
                          <div className="py-8 text-center">
                            <div className="text-slate-500">No recent activity</div>
                            <Link href="/" className="mt-3 text-sm font-medium text-blue-600 hover:text-blue-700 inline-block">
                              Start your first comparison ‚Üí
                            </Link>
                          </div>
                        ) : (
                          computed.latest.map((item) => (
                            <ActivityItem key={item.id} item={item} />
                          ))
                        )}
                      </div>
                    </div>
                  </ModernCard>

                  {/* Right Column */}
                  <div className="space-y-6">
                    {/* Risk Distribution */}
                    <ModernCard>
                      <div className="p-5">
                        <h2 className="text-lg font-semibold text-slate-900 mb-4">Risk Distribution</h2>
                        {canSeeRisk ? (
                          <div className="space-y-5">
                            <div className="space-y-4">
                              {/* High Risk */}
                              <div>
                                <div className="flex justify-between text-sm mb-2">
                                  <span className="font-medium text-red-700">High Risk</span>
                                  <span className="font-semibold text-slate-900">{computed.high}</span>
                                </div>
                                <div className="h-2 bg-red-100 rounded-full overflow-hidden">
                                  <div 
                                    className="h-full bg-gradient-to-r from-red-500 to-red-600 rounded-full transition-all duration-500" 
                                    style={{ width: `${(computed.high / Math.max(1, computed.comparisons)) * 100}%` }}
                                  />
                                </div>
                              </div>
                              {/* Medium Risk */}
                              <div>
                                <div className="flex justify-between text-sm mb-2">
                                  <span className="font-medium text-amber-700">Medium Risk</span>
                                  <span className="font-semibold text-slate-900">{computed.med}</span>
                                </div>
                                <div className="h-2 bg-amber-100 rounded-full overflow-hidden">
                                  <div 
                                    className="h-full bg-gradient-to-r from-amber-500 to-amber-600 rounded-full transition-all duration-500" 
                                    style={{ width: `${(computed.med / Math.max(1, computed.comparisons)) * 100}%` }}
                                  />
                                </div>
                              </div>
                              {/* Low Risk */}
                              <div>
                                <div className="flex justify-between text-sm mb-2">
                                  <span className="font-medium text-emerald-700">Low Risk</span>
                                  <span className="font-semibold text-slate-900">{computed.low}</span>
                                </div>
                                <div className="h-2 bg-emerald-100 rounded-full overflow-hidden">
                                  <div 
                                    className="h-full bg-gradient-to-r from-emerald-500 to-emerald-600 rounded-full transition-all duration-500" 
                                    style={{ width: `${(computed.low / Math.max(1, computed.comparisons)) * 100}%` }}
                                  />
                                </div>
                              </div>
                            </div>
                            
                            <div className="pt-4 border-t border-slate-100">
                              <div className="flex items-center justify-between">
                                <div className="text-sm text-slate-600">
                                  <span className="font-semibold text-slate-900">{computed.riskPercentage}%</span> require attention
                                </div>
                                <div className="text-xs px-2 py-1 bg-slate-100 text-slate-600 rounded">
                                  Total: {computed.comparisons}
                                </div>
                              </div>
                            </div>
                          </div>
                        ) : (
                          <div className="text-center py-8">
                            <ShieldExclamationIcon className="h-12 w-12 text-slate-300 mx-auto mb-3" />
                            <div className="text-slate-500">Risk data is not available for your role</div>
                          </div>
                        )}
                      </div>
                    </ModernCard>

                    {/* Quick Actions */}
                    <ModernCard>
                      <div className="p-5">
                        <h2 className="text-lg font-semibold text-slate-900 mb-4">Quick Actions</h2>
                        <div className="space-y-3">
                          <Link
                            href="/"
                            className="block w-full bg-gradient-to-r from-blue-600 to-blue-700 text-white hover:from-blue-700 hover:to-blue-800 active:scale-[0.98] shadow-sm hover:shadow rounded-lg font-semibold transition-all duration-200 flex items-center justify-center gap-2 px-4 py-3 text-sm"
                          >
                            <DocumentMagnifyingGlassIcon className="h-4 w-4" />
                            Compare New Documents
                          </Link>
                          
                          <Link
                            href="/reports/generate"
                            className="block w-full bg-gradient-to-r from-emerald-600 to-emerald-700 text-white hover:from-emerald-700 hover:to-emerald-800 active:scale-[0.98] shadow-sm hover:shadow rounded-lg font-semibold transition-all duration-200 flex items-center justify-center gap-2 px-4 py-3 text-sm"
                          >
                            <DocumentArrowDownIcon className="h-4 w-4" />
                            Generate Report
                          </Link>
                          
                          <button
                            onClick={() => setActiveTab("rag")}
                            className="block w-full bg-gradient-to-r from-purple-600 to-purple-700 text-white hover:from-purple-700 hover:to-purple-800 active:scale-[0.98] shadow-sm hover:shadow rounded-lg font-semibold transition-all duration-200 flex items-center justify-center gap-2 px-4 py-3 text-sm"
                          >
                            <ArchiveBoxIcon className="h-4 w-4" />
                            Manage RAG Storage
                          </button>
                        </div>
                      </div>
                    </ModernCard>
                  </div>
                </div>
              </>
            )}

            {/* RAG Storage Tab */}
            {activeTab === "rag" && (
              <div className="space-y-6">
                <RagDocumentManager />
                
                {/* RAG Information */}
                <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                  <ModernCard>
                    <div className="p-5">
                      <div className="flex items-start justify-between">
                        <div>
                          <div className="text-sm text-slate-500 font-medium mb-2">Storage Type</div>
                          <div className="text-lg font-bold text-slate-900">ChromaDB</div>
                        </div>
                        <div className="p-3 rounded-lg bg-gradient-to-br from-purple-50 to-purple-100 text-purple-600">
                          <ArchiveBoxIcon className="h-6 w-6" />
                        </div>
                      </div>
                      <div className="mt-4 pt-4 border-t border-slate-100">
                        <div className="text-sm text-slate-500">
                          Vector database for AI embeddings
                        </div>
                      </div>
                    </div>
                  </ModernCard>
                  
                  <ModernCard>
                    <div className="p-5">
                      <div className="flex items-start justify-between">
                        <div>
                          <div className="text-sm text-slate-500 font-medium mb-2">AI Model</div>
                          <div className="text-lg font-bold text-slate-900">Embedding</div>
                        </div>
                        <div className="p-3 rounded-lg bg-gradient-to-br from-blue-50 to-blue-100 text-blue-600">
                          <DocumentChartBarIcon className="h-6 w-6" />
                        </div>
                      </div>
                      <div className="mt-4 pt-4 border-t border-slate-100">
                        <div className="text-sm text-slate-500">
                          For semantic search and analysis
                        </div>
                      </div>
                    </div>
                  </ModernCard>
                  
                  <ModernCard>
                    <div className="p-5">
                      <div className="flex items-start justify-between">
                        <div>
                          <div className="text-sm text-slate-500 font-medium mb-2">Query Support</div>
                          <div className="text-lg font-bold text-slate-900">Available</div>
                        </div>
                        <div className="p-3 rounded-lg bg-gradient-to-br from-emerald-50 to-emerald-100 text-emerald-600">
                          <DocumentMagnifyingGlassIcon className="h-6 w-6" />
                        </div>
                      </div>
                      <div className="mt-4 pt-4 border-t border-slate-100">
                        <div className="text-sm text-slate-500">
                          Natural language querying
                        </div>
                      </div>
                    </div>
                  </ModernCard>
                </div>
              </div>
            )}
          </main>
        </div>
      </div>
    </div>
  );
}